<template lang="">
  <div class="weather_app">
    <SearchView @searchQuery="searching" :setDefault="setDefault"></SearchView>

    <div
      class="d-flex justify-content-center align-items-center not_found"
      v-if="this.noResults == true || this.results.message == 'invalid query'"
    >
      <h2>No Results Found</h2>
    </div>

    <div class="text-center mt-3 mb-3 welcome" v-if="welcome">
      <img src="../assets/img/Weather_db.png" alt="weatherdb">
    </div>

    <div v-if="loading" class="d-flex justify-content-center align-items-center loading">
      <swapping-squares-spinner
        :animation-duration="1000"
        :size="65"
        color="#931e81"
      />
    </div>

    <div
      class="weatherdb"
      v-if="this.found == true && this.results.message != 'invalid query'"
    >
      <div class="top">
        <div class="resume">
          <div class="resume_top">
            <img v-if="today.iconURL" class="img-fluid" :src="today.iconURL" :alt="today.comment" />
            <div class="degree">
              <p>{{ todayTemp.c }}<span>C</span></p>
              <p>{{ todayTemp.f }}<span>F</span></p>
            </div>
          </div>
          <div class="resume_center">
            <p>Weather Today in</p>
            <h2>{{ this.apiQuery }}</h2>
          </div>
          <div class="resume_bottom">
            <div class="comment">
              <span>{{ today.comment }}</span>
            </div>
          </div>
        </div>
        <div class="details">
          <div class="details_top">
            <h3>{{ results.region }}</h3>
          </div>
          <div class="details_main">
            <div class="one">
              <div class="tile">
                <span> Wind</span>
                <div class="icon"><i class="fa-solid fa-wind"></i></div>
                <!--<div class="value" v-for="wind in today.wind" :key="wind.id"><p>{{wind}}</p></div>-->
                <div class="value">
                  <p>{{ todayWinds.km }}</p>
                </div>
              </div>
              <div class="tile">
                <span> Humidity</span>
                <div class="icon"><i class="fa-solid fa-droplet"></i></div>
                <div class="value">
                  <p>{{ today.humidity }}</p>
                </div>
              </div>
            </div>
            <div class="two">
              <div class="tile">
                <span> Precipitation</span>
                <div class="icon"><i class="fa-solid fa-umbrella"></i></div>
                <div class="value">
                  <p>{{ today.precip }}</p>
                </div>
              </div>
              <div class="tile">
                <span> Last Update</span>
                <div class="icon"><i class="fa-regular fa-clock"></i></div>
                <div class="value">
                  <p>{{ today.dayhour }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bottom">
        <div class="next_days">
          <div class="day" v-for="day in nextDays" :key="day.id">
            <span class="day_day">{{ day.day }}</span>
            <img v-if="day.iconURL" class="img-fluid" :src="day.iconURL" :alt="day.comment" />
            <div class="temp">
              <div class="max">
                <div class="icon">
                  <i class="fa-solid fa-arrow-trend-up"></i>
                </div>
                <div
                  class="degree"
                  v-for="(value, key) in day.max_temp"
                  :key="key.id"
                >
                  <p>{{ value }} {{ key }}</p>
                </div>
              </div>
              <div class="min">
                <div class="icon">
                  <i class="fa-solid fa-arrow-trend-down"></i>
                </div>
                <div
                  class="degree"
                  v-for="(value, key) in day.min_temp"
                  :key="key.id"
                >
                  <p>{{ value }} {{ key }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="logo">

            <img  src="../assets/img/Weather_db.png" alt="weatherdb">
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import SearchView from "../components/WeatherApp/Search.vue";
import { SwappingSquaresSpinner } from "epic-spinners";
export default {
  name: "WeatherView",
  components: {
    SearchView,
    SwappingSquaresSpinner,
  },
  data() {
    return {
      apiQuery: "",
      apiWeatherURL: "https://weatherdbi.herokuapp.com/data/weather/",
      results: {},
      today: {},
      todayWinds: {},
      todayTemp: {},
      nextDays: [],
      nextDaysMax: {},
      welcome: true,
      noResults: false,
      found: false,
      loading: false,
    };
  },
  methods: {
    // funzione che permette di eseguire la chiamata API avviando la ricerca di query(del testo inserito dall'utente)
    searching(apiQuery) {
      if (apiQuery != "") {
        this.apiQuery = apiQuery;
        this.loading = true;
        this.welcome = false;
        this.noResults = false;
        this.found = false;
        axios
          .get(this.apiWeatherURL + this.apiQuery)
          .then((resp) => {
            this.results = resp.data;
            //console.log(this.results);
            this.today = this.results.currentConditions;
            //console.log(this.today);
            this.todayWinds = this.today.wind;
            //console.log(this.todayWinds);
            this.todayTemp = this.today.temp;
            //console.log(this.todayTemp);
            this.nextDays = this.results.next_days;
            //console.log(this.nextDays);
            this.loading = false;
            if (this.results.length === 0) {
              this.noResults = true;
            } else {
              this.found = true;
            }
          })
          .catch((error) => {
            this.loading = false;
            console.log(error);
          });
      }
    },
    //funzione per resettare gli array
    reset() {
      this.loading = false;
      this.found = false;
      this.welcome = true;
      this.results = [];
    },
    //funzione per la situazione di default,viene passata come prop per il bottone reset
    setDefault() {
      this.reset();
      //this.popular();
    },
    substringText(string, min, max) {
      return string.substring(min, max);
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/style/app.scss";
.weather_app {
  min-height: 100vh;
  background: $body-bg;
  padding: 50px;
  color: $color-main;
  @media all and (max-width: 990px) {
    padding: 50px 10px 50px 10px;
  }
  h1 {
    color: $color-main;
    text-align: center;
  }
}
.not_found {
  height: 75vh;
}
.loading {
  height: 75vh;
}
.welcome {
  height: 100vh;
  background-image: url(../assets/img/weatherdb.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  img {
    width: 300px;
    @media all and (max-width: 330px) {
      width: 150px;
    }
  }
}
.weatherdb {
  min-height: 50vh;
  overflow: hidden;
  color: #000000;
  @include flex($justify-content: space-between, $flex-direction: column);
  .top {
    @include flex($justify-content: space-between);
    min-height: 40vh;
    .resume {
      //background: yellow;
      background: #1f9ebe;
      flex-basis: 35%;
      min-height: 40vh;
      border-right: 3px solid #fff;
      border-left: 3px solid #fff;
      transition: all 0.3s ease;
      &:hover {
        box-shadow: 0 1px 10px rgba(0, 0, 0, 10);
      }
      @include flex($justify-content: space-between, $flex-direction: column);
      .resume_top {
        @include flex(
          $justify-content: space-evenly,
          $align-items: center,
          $flex-direction: column,
          $flex-wrap: wrap-reverse
        );
        .degree {
          @include flex($align-items: center, $flex-direction: column);
          color: rgb(255, 255, 255);
        }
      }
      .resume_center {
        text-align: center;
        p {
          font-size: 0.8em;
        }
        h2 {
          text-transform: uppercase;
          font-size: 1.4em;
        }
      }
      .resume_bottom {
        .comment {
          text-align: center;
          background: rgba(34, 37, 232, 0.407);
          color: rgb(255, 255, 255);
          text-transform: uppercase;
          font-size: 0.8rem;
          border-bottom: 3px solid #fff;
        }
      }
    }
    .details {
      flex-basis: 65%;
      min-height: 40vh;
      .details_top {
        h3 {
          max-height: 10vh;
          font-size: 0.6rem;
          letter-spacing: 0.2rem;
          text-transform: uppercase;
          color: #000000;
          background: #88ed85;
          border-left: 3px solid #fff;
          border-right: 3px solid #fff;
        }
      }
      .details_main {
        @include flex($justify-content: space-between);
        .one,
        .two {
          flex-basis: calc(100% / 2);
          height: 40vh;
          .tile {
            border: 3px solid #fff;
            height: 20vh;
            transition: all 0.3s ease;
            background: #00d7b2;
            text-align: center;
            &:hover {
              box-shadow: 0 1px 10px rgba(0, 0, 0, 10);
            }
            @include flex(
              $justify-content: center,
              $align-items: center,
              $flex-direction: column
            );
            .icon {
              color: #fff;
            }
            .value {
              color: #fff;
              font-size: 0.8rem;
            }
            i {
              font-size: 1.5em;
            }
            span {
              font-size: 0.8rem;
              @media all and (max-width: 330px) {
                display: none;
              }
            }
          }
        }
      }
    }
  }
  .bottom {
    //@include flex($justify-content: space-between);
    min-height: 10vh;
    .next_days {
      @include flex($justify-content: space-between);
      //background: blueviolet;
      min-height: 10vh;
      //flex-basis: calc(100% / 8);
      flex-wrap: wrap;
      .day {
        //flex-basis: calc(100% / 8);
        background: #8cdde8;
        border: 3px solid #fff;
        transition: all 0.3s ease;
        min-width: 50px;
        width: calc(100% / 4);
        &:hover {
          box-shadow: 0 1px 10px rgba(0, 0, 0, 10);
        }
        @include flex(
          $justify-content: space-evenly,
          $align-items: center,
          $flex-direction: column,
          $flex-wrap: wrap-reverse
        );
        .day_day {
          font-size: 0.9rem;
          @media all and (max-width: 330px) {
            font-size: 0.7rem;
          }
        }
        .temp {
          display: flex;
          @media all and (max-width: 330px) {
            display: none;
          }
          .max {
            margin-right: 10px;
            color: rgba(220, 35, 35, 0.805);
          }
          .min {
            color: rgba(38, 38, 215, 0.768);
          }
          .degree {
            font-size: 0.8rem;
            //@include flex($align-items: center, $flex-direction: column);
            color: rgb(0, 0, 0);
          }
        }
      }
    }
  }
  .logo {
    height: 10vh;
    display: flex;
    justify-content: center;
    img {
      width: 200px;
    }
  }
}
</style>
